// 1 
const fightTimeInfo = (data) => {
    let [client, server, time] = data[2].split("::");
    return {
        start: data[1], // isStart 
        client, // zhcn_hd
        server, // 
        time, // 
        during: data[3], // during 
        map: data[4], // map 
        raw: data[2], // PVP
    };
};
// 2,3,6,7,10,11 logID
const singleIDInfo = (data) => {
    return {
        id: data[1], //id
    };
};
// 4 
const playerInfo = (data) => {
    let { 1: id, 2: name, 3: school, 4: mount, 5: equipScore, 6: _equips, 7: _talents, 8: guid } = data;
    // 
    let equips = [];
    for (let eIndex in _equips) {
        let equip = _equips[eIndex];
        if (!equip) continue;
        let embedding = [];
        for (let i = 1; i <= 3; i++) if (equip[5]?.[i]) embedding.push(`${equip[5][i][1]}_${equip[5][i][2]}`);
        let colorful = equip[5][0] ? `${equip[5][0][1]}_${equip[5][0][2]}` : "";
        let enhance = [equip[6], equip[7], equip[8]].filter((x) => x > 0);
        equips.push({
            position: equip[1], //position  
            id: `${equip[2]}_${equip[3]}`, //item      ID
            strength: equip[4], //strength  
            embedding: embedding, //embedding 
            stone: colorful, //colorful  
            enhance: enhance, //enhance   
        });
    }
    // 
    let talents = [];
    for (let tIndex in _talents) {
        let talent = _talents[tIndex];
        talents.push({
            order: parseInt(tIndex), //order      1-12
            position: talent[1], //position   1-5
            skill: `${talent[2]}_${talent[3]}`, // id_level
        });
    }
    return {
        id, //id   id
        name, //name 
        school, //school 
        mount, //mount 
        equipScore, //equipScore 
        equips, //equips 
        talents, //talents 
        guid, //guid guid
    };
};
